* {
  margin: 0;
  padding: 0;
  cursor: default;
}

body {
  margin: 0;
  padding: 0;
  min-width: 500px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

input {
  width: 15vw;
  min-width: 85px;
}

.jumbotron {
  position: relative;
  background-color: rgba(0, 0, 0, .35) !important;
}

.bg-home {
  background-image: url('home.jpg');
}

.bg-about {
  background-image: url('about.jpg');
}

.bg-services {
  background-image: url('services.jpg');
}

.bg-contact {
  background-image: url('contact.jpg');
}

.jumbotron-bg {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  background-position: center;
  background-size: cover;
  z-index: -1;
}

.nav-logo {
  width: 8%;
}

.contact-logo {
  width: 25vw;
  min-width: 170px;
}

.page-hight-100 {
  height: 80vh;
}

/* SEKCJA KONTAKT */
.form-contact {
  text-align: center;
}

.form-label {
  display: block;
  padding: 5px 0;
}

.form-input {
  margin: 0 5px;
  padding: 2px;
}

.form-area {
  margin: 10px auto;
  padding: 2px;
}

input#checkbox {
  width: auto;
  min-width: auto;
}

textarea {
  width: 30vw;
  min-width: 180px;
}

.red {
  color: crimson;
}

.green {
  color: green;
}